.button{
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #34495e;
    transition: color, background-color;
    transition-duration: 0.5s;
    border-radius: 5px;
    cursor: pointer;
}

.button:hover{
    background-color: #34495e;
    color: white;
}